.form-label {
  @apply mb-2 block text-gray-700 select-none;
}

.form-input,
.form-textarea,
.form-select {
  @apply p-2 leading-normal focus:ring-0 outline-none block w-full border border-gray-300 text-gray-700 bg-white font-sans rounded text-left appearance-none relative focus:border-indigo-400 disabled:bg-gray-300 disabled:bg-opacity-60 disabled:cursor-not-allowed;

  &::placeholder {
    @apply text-gray-500 opacity-100;
  }
}

.form-input-checkbox {
  @apply appearance-none h-4 w-4 border border-gray-300 rounded-sm bg-white text-purple-600 checked:bg-purple-600 checked:border-purple-600 focus:outline-none focus:ring-purple-600 focus:border-purple-600 transition duration-200 mt-1 align-top bg-no-repeat bg-center bg-contain float-left cursor-pointer;
}

.form-select {
  @apply pr-7;

  background-image: url("data:image/png;base64,");
  background-size: 0.7rem;
  background-repeat: no-repeat;
  background-position: right 0.7rem center;

  &::-ms-expand {
    @apply opacity-0;
  }
}

.form-input.error,
.form-textarea.error,
.form-select.error {
  @apply border-red-500 focus:ring focus:ring-red-200;
}

.form-error {
  @apply text-red-500 mt-2 text-sm;
}

.form-footer {
  @apply flex items-center justify-end px-8 py-4 bg-gray-50 border-t border-gray-100;
}

.form-action-container {
  @apply flex items-center justify-end px-8 py-4 bg-gray-50 border-t border-gray-100;
}
